.gridTask
{
    margin-top: 1em;
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-rows: minmax(150px,200px);
    justify-content: center;
    align-items: center;
    padding: 1em;
    gap:1em ;
    
}
.task{
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 0.5em;

    padding: 1em;
    border-radius: 10px;

    background-color: var(--main-color-2);

    font-size: 1.25rem;
    color: var(--main-color-5);
    z-index: 999;
}
.task-info{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    flex: 3;
    height: 100%;
    border-left: solid 1px var(--main-color-5);
}
.task-btn-container{
    flex: 0.5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
    
}
.task-btn{
    display:flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 3em;
    height: 3em;
    border:0.1em solid var(--main-color-4);
   
    box-sizing: border-box;

    background-color:var(--main-color-5);
   
    font-weight:800;
    color: var(--main-color-2);
    text-align:center;
    font-size: 0.75em;

    

}

.addTask-btn
{
    font-size: 1rem;
}

/******************/

/*********STATE STYLE /*********/
.task-btn:hover{
    background-color: var(--main-color-2);
    color: var(--main-color-5);

}

.task-done{
    background-color: #00DC71;
    color: var(--main-color-1);
}

.task-done-title{
    border-left: solid 3px var(--main-color-1);
}
.editable {
    display: flex;
    align-items: center;
    justify-content: center;
    border: solid 1px white ;
    background-color: var(--main-color-1);
    cursor: text;
    width: 100%;
  }


/******************/

