input{
    text-align: center;
    height: 2em;
}
.addTask-form
{ 
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    width: auto;
    padding: 1.5em;
    height: 20em;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    gap: 1em;
    

    background-color: var(--main-color-3);
    z-index: 999;
}
.overlay{
    position: fixed;
    margin: -2em;
    width: 100%;
    height: 100%;
  
    background-color: rgba(152, 151, 153, 0.7);
  
    z-index: 1; /* Ensure the overlay appears above other content */
  }

  .addProject-form{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
  }
  .btn-container
  {
    display: flex;
    justify-content: space-around;
  }
.addNewProject-btn{
  display:inline-block;
    padding:0.35em 1.2em;
    border:0.1em solid var(--main-color-1);
    margin:0 0.3em 0.3em 0;
    border-radius:0.12em;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:800;
    background-color:var(--main-color-5);
    color: var(--main-color-1);
    text-align:center;
    transition: all 0.2s;
    font-size: 0.75rem;
    border-radius: 5px;
    width: auto;
