    box-sizing: border-box;
    padding: 0;
    margin: 0;
}


/**** CSS variables *****/
:root{
    --main-color-1: rgb(34, 30, 43);
    --main-color-2: rgb(67, 59, 81);
    --main-color-3: rgb(117, 94, 132);
    --main-color-4: rgb(180, 138, 196);
    --main-color-5: rgb(255, 199, 244);

    font-family: Circular,-apple-system,BlinkMacSystemFont,Roboto,"Helvetica Neue",sans-serif;
}

/*********LAYOUT STYLE***********/
#app
{
    height: 100vh;
    width: 100vw;

    display:grid;
    grid-template-columns: 300px 1fr;
    grid-template-areas: 
    "sideBar todoContent";
    
    gap: 0.5em;

    background-color: var(--main-color-1);
    font-size: 1rem;
}

.sideBar
{
    display: flex;
    flex-direction: column;

    background-color: var(--main-color-3);
    
    grid-area: sideBar;
    gap: 1em ;
}
.sideBar-title
{
    background-color: var(--main-color-4);
    padding: 1.5rem;
}
.sideBar-action{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    padding: 0.5em;
}

.sideBar-projectBtn
{
    display:inline-block;
    padding:0.35em 1.2em;
    border:0.1em solid var(--main-color-3);
    margin:0 0.3em 0.3em 0;
    border-radius:0.12em;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:800;
    color:var(--main-color-1);
    background-color: var(--main-color-3) ;
    text-align:center;
    transition: all 0.2s;
    font-size: 1rem;
}

.sideBar-addProject
{
    border-top: 2px solid var(--main-color-2);
    border-bottom: 2px solid var(--main-color-2);
    
    padding: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.todo-content
{
    display: grid;
    grid-template-rows: 100px 1fr;
    grid-template-columns: 20
    0px 1fr;
    background-color: var(--main-color-5);
    grid-area:todoContent ;
    padding: 2em;
}
.addTask-btn{
    display:inline-block;
    padding:0.35em 1.2em;
    border:0.1em solid var(--main-color-1);
    margin:0 0.3em 0.3em 0;
    border-radius:0.12em;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:800;
    background-color:var(--main-color-5);
    color: var(--main-color-1);
    text-align:center;
    transition: all 0.2s;
    font-size: 1.5rem;
    border-radius: 5px;
    width: 10em;

}

/**********************/

/*****STATE STYLE********/
.sideBar-projectBtn:hover{
    
    background-color:var(--main-color-4);
    color: var(--main-color-5);
    border-radius: 10px;
}



.addTask-btn:hover{
    
    background-color: var(--main-color-1) ;
    color:var(--main-color-5);

    border-radius: 10px;
}

.btn-add-focus
{
    background-color:var(--main-color-4);
    color: var(--main-color-5);
    border-radius: 10px;
}

.is-hidden{
    display: none !important;
}

